body, input {
  font-family: sans-serif;
  background-color: white;
}

a {
  text-decoration: none;
  transition: all 200ms linear;
}

a:focus {
  outline: none;
}

#tabs {
  font-size: 24px;
  margin-bottom: 10px;
}

#tabs a {
  color: #a6a6a6;
  padding: 0 10px;
}

#tabs a:hover {
  color: #005a9e;
}

#tabs a:active, #tabs a.active {
  color: #0078d7;
}

#tones {
  margin-bottom: 20px;
}

#tones a {
  display: inline-block;
  width: 15px;
  height: 15px;
  transition: all 200ms ease;
  border-radius: 50%;
  border: 1px solid transparent;
  margin: 0 5px;
}

#tones a.tone-0 {
  background-color: #ffcf11;
}

#tones a.tone-1 {
  background-color: #fae3c3;
}

#tones a.tone-2 {
  background-color: #fed0ac;
}

#tones a.tone-3 {
  background-color: #dba373;
}

#tones a.tone-4 {
  background-color: #a88054;
}

#tones a.tone-5 {
  background-color: #5f4e43;
}

#tones a:hover, #tones a.active {
  transform: scale(1.44);
}

