#content-main {
  max-width: 960px;
  margin: 0 auto;
  text-align: center;
  padding: 10px;
}

footer {
  font-size: 12px;
  margin-top: 20px;
}

#galleries.history #emoji-gallery {
  display: none;
}

#galleries.emoji #history-gallery {
  display: none;
}

.joypixels {
  width: 32px;
  height: 32px;
  margin: 0;
  transition: all 100ms ease;
  cursor: pointer;
  transform: scale(0.75);
}

.joypixels:hover {
  transform: scale(0.9);
}

.joypixels:active {
  opacity: 0.8;
}

.search .joypixels {
  display: none;
}

.search .match {
  display: inline-block;
}

#search {
  margin-bottom: 10px;
}

#search input {
  width: 100%;
  box-sizing: border-box;
}
